<template>
  <div class="numberBorderBox">
    <button @click="changeAction(1)">+</button>
    <!-- 使用change事件，失焦或回车时触发，就是当input的值正式发生改变的时候触发 -->
    <div><input type="number" :value="value" @change="changeQuantily"></div>
    <button @click="changeAction(-1)">-</button>
  </div>
</template>

<script>
import { Toast } from 'vant'
export default {
  name: 'numberBorder',
  methods: {
    changeAction (num) {
      // 数字提前比对
      if ((this.value + num) < 1) {
        return Toast('最低1件')
      } else {
        this.$emit('input', this.value + num)
      }
    },
    changeQuantily (e) {
      if (e.target.value >= 1 && /^[^0]\d*$/.test(e.target.value)) {
        this.$emit('input', e.target.value)
      } else {
        Toast('注意件数规范')
        e.target.value = this.value
      }
    }
  },
  props: {
    value: {
      type: Number,
      default: 1,
      required: true
    }
  }
}
</script>

<style lang="less" scoped>
.numberBorderBox {
  width: 80px;
    display: flex;
    align-items: center;
    font-size: 16px;
    button {
        border: 1px solid black;
        border-radius: 2px;
        color: black;
        background-color: #fff;
        width:20px;
        height: 20px;
        &:active {
          background-color: #ececec;
        }
    }
    div {
        width: 40px;
        input {
          width: 100%;
          text-align: center;
          outline: none;
          border: 0;
        }
    }
}
</style>
