<template>
  <div class="box">
    <NavBar>
      <template #title>
        购物车
      </template>
    </NavBar>
    <div class="div" v-if="cartList.length !== 0 && isToken">
      <div class="controller">
        <!-- 编辑模块 -->
        <div class="total">
          <div class="left">共<span>{{cartList.length}}</span>件商品</div>
          <div class="right" @click="editAction" v-if="isBtnShow">
            <van-icon name="edit" />
            <span>编辑</span>
          </div>
          <div v-else @click="accomplishAction">完成</div>
        </div>
        <!-- 购物车物品模块 -->
        <div class="cartItem">
          <cartItem v-for="item in cartList" :key="item.id" :cartData="item"></cartItem>
        </div>
      </div>
      <div class="account">
        <div class="left">
        <label>
          <van-checkbox v-model="isSelectAll"></van-checkbox>
          全选
        </label>
        </div>
        <div class="right">
          <span>合计: ￥<strong>{{total}}</strong></span>
          <button v-if="isBtnShow" :class="{btnBgc: accountTotal === 0}" :disabled="accountTotal === 0" @click="checkOutAction">结算({{accountTotal}})</button>
          <button v-else @click="delCartItem" :class="{btnBgc: accountTotal === 0}" :disabled="accountTotal === 0">删除</button>
        </div>
      </div>
    </div>
    <div class="defaultBox" v-else>
      <div class="text">购物车空空如也</div>
      <div @click="$router.push('/')" class="guang">去逛逛~~</div>
    </div>
  </div>
</template>

<script>
import cartItem from '@/components/cartItem.vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'

export default {
  data () {
    return {
      isBtnShow: true
    }
  },
  name: 'cartIndex',
  components: {
    cartItem
  },

  methods: {
    ...mapActions('cart', ['getCartGoods', 'delCartItem']),
    ...mapMutations('cart', ['modify']),
    editAction () {
      this.isBtnShow = !this.isBtnShow
      // 点击商品全部未选中
      this.modify(false)
    },
    accomplishAction () {
      this.isBtnShow = !this.isBtnShow
      // 点击商品全部选中
      this.modify(true)
    },
    checkOutAction () {
      const arr = this.isDecisionTotal.map(item => item.id).join(',')
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'cart',
          cartIds: arr
        }
      })
    }
  },
  created () {
    this.getCartGoods()
  },
  computed: {
    ...mapGetters('cart', ['total', 'isSelectAll', 'accountTotal', 'isDecisionTotal']),
    cartList () {
      return this.$store.state.cart.cartList
    },
    isSelectAll: {
      get () {
        return this.cartList.every(item => Boolean(item.isDecision))
      },
      set (e) {
        this.modify(e)
      }
    },
    isToken () {
      return this.$store.state.user.userInfo
    }
  }
}
</script>

<style lang="less" scoped>
.controller {
  overflow: auto;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 100px;
  padding: 0 10px;
  .total {
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    .left {
      span {
        margin: 0 3px;
        color: red;
      }
    }
    >div {
      &:nth-child(2) {
        i {
        font-size: 20px;
        margin-right: 5px;
      }
      }
    }
  }
}
.account {
  position: fixed;
  bottom: 50px;
  left: 0;
  right: 0;
  height: 50px;
  padding: 0 10px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .left {
    font-size: 20px;
    label {
      display: flex;
      .van-checkbox {
        font-size: 20px;
        margin-right: 3px;
      }
    }
  }
  .right {
    font-size: 16px;
    span {
      strong {
        font-size: 20px;
        color: red;
        font-weight: 500;
      }
      margin-right: 10px;
    }
    button {
      border: 0;
      width: 100px;
      height: 38px;
      background-color: #fd411a;
      color: #fff;
      border-radius: 19px;
    }
  }
}
.defaultBox {
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  color: gray;
  .guang {
    color: #fd411a;
  }
}
.btnBgc {
  background-color: #ff9e77 !important;
}
</style>
