<template>
  <div class="box">
    <div class="checkbox">
      <van-checkbox v-model="isSelect"></van-checkbox>
    </div>
    <div class="img" @click="$router.push(`/prodetail/${cartData.goods.goods_id}`)">
        <img :src="cartData.goods.goods_image" alt="">
    </div>
    <div class="detail">
        <h3>
            <p class="ellipsis" @click="$router.push(`/prodetail/${cartData.goods.goods_id}`)">
                {{cartData.goods.goods_name}}
            </p>
        </h3>
        <div>
            <div class="price">
                <span>￥</span>
                <strong>{{cartData.goods.goods_price_max}}</strong>
            </div>
            <numberBorder :value="cartData.goods_num" @input="num"></numberBorder>
        </div>
    </div>
  </div>
</template>

<script>
import numberBorder from '@/components/numberBorder.vue'
export default {
  name: 'cartItem',
  components: {
    numberBorder
  },
  methods: {
    num (e) {
      this.$store.dispatch('cart/updateCartItem', {
        goodsId: this.cartData.goods.goods_id,
        goodsNum: e,
        goodsSkuId: this.cartData.goods_sku_id
      })
    }
  },
  props: {
    cartData: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    isSelect: {
      get () {
        return Boolean(this.cartData.isDecision)
      },
      set (e) {
        this.$store.commit('cart/changeIsDecision', {
          goodsId: this.cartData.id,
          bool: e
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.box {
    margin: 0 0 10px 0;
    border-radius: 5px;
    padding: 15px 15px 15px 7px;
    background-color: #fff;
    display: flex;
    .checkbox {
        display: flex;
        justify-content: center;
        align-items: center;
        input {
            width: 20px;
            height: 20px;
        }
    }
    .img {
        margin:0 15px 0 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100px;
        img {
            width: 100px;
        }
    }
    .detail {
        display: flex;
        flex-direction: column;

        h3 {
            flex: 1;
            font-size: 16px;
            font-weight: 500;
            p {
                padding-right: 8px;
            }
        }
        > div {
            flex: 1;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            .price {
                color: red;
                span {
                    font-size: 16px;
                }
                strong {
                    font-size: 18px;
                }
            }
        }
    }
}

</style>
